<template>
  <div name="ChartFD" id="chart" class="chart2">
    <div>
      <div class="form-group">
        <label for="date">日期：</label>
        <input type="date" name="date" class="form-control" v-model="req.key">
      </div>
      <div class="form-group ">
        <label for="t-value">体温：</label>
        <input type="number" name="t-value" class="form-control" v-model="req.value" placeholder="点击输入体温" @keyup.enter="addorEdit(req.key,req.value)">
      </div>
    </div>
    <div class="form-inline">
      <div class="form-group">
        <label for="m-month">记录月：</label>
        <input type="month" class="form-control" name="m-month" v-model="month" />
      </div>
      <button class="btn btn-primary" @click="initChart">Paint</button>
    </div>
    <div class="chart-ctn" id="chartsCtn"></div>
    <div class="producer">
      <p>Product By: 芥末哥</p>
    </div>
  </div>
</template>
<script>
import eCharts from 'echarts';
import moment from 'moment';
import DB from '../services/db.js';
export default {
  name: 'ChartFD',
  data() {
    return {
      month: moment().format('YYYY-MM'),
      req: {
        key: moment().format('YYYY-MM-DD'),
        value: ''
      },
      list: []
    };
  },
  methods: {
    initData: async function() {
      const req = await DB.get(this.req.key);
      if (req) {
        this.req.value = req.value;
      } else {
        // add
        console.log(req);
      }
    },
    addorEdit: async function(time, value) {
      const req = await DB.put({ time, value });
      console.log(req);
    },
    initChart: async function() {
      const range = {
        start: moment()
          .startOf('month')
          .format('YYYY-MM-DD'),
        end: moment()
          .endOf('month')
          .format('YYYY-MM-DD')
      };
      const list = await DB.indexFilter('time', range);
      console.log(list);
      let xData = [];
      let yData = [];
      list.forEach(e => {
        xData.push(e.key);
        yData.push(e.value.value);
      });
      console.log(xData);
      const myCharts = eCharts.init(document.getElementById('chartsCtn'));
      myCharts.setOption({
        xAxis: {
          min: 'dataMin',
          max: 'dataMax',
          data: xData
        },
        yAxis: {
          type: 'value',
          min: 'dataMin',
          max: 'dataMax'
        },
        series: {
          type: 'line',
          data: yData
        }
      });
    }
  },
  created: function() {
    document.title = '芥末芥末';
    this.initData();
  }
};
</script>
<style lang="scss" scoped>
#chart {
  background-color: beige;
  text-align: center;
}
.chart2 {
  display: flex;
  flex-direction: column;
  height: 100%;
  input {
    text-align: center !important;
  }
}
.chart-ctn {
  height: 480px;
  width: 100%;
}
.producer {
  position: absolute;
  width: 100%;
  bottom: 0;
  p {
    text-align: right;
    padding-right: 15px;
  }
}
</style>



